import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
export default new Vuex.Store({
    state:{
        topClass:false,
        snowStart:false,
        Text_1Over:false,
        Text_2Over:false,
        Text_3Over:false,
        Text_4Over:false,


    },
    mutations:{
        changeValue(state,{prop,newVal}){
            // console.log(prop,newVal)
            state[prop] = newVal
        }
    },
    actions:{

    },

})


